import React,{useState} from 'react'
import Tab from 'react-bootstrap/Tab'
import Tabs from 'react-bootstrap/Tabs'


 function ControlledTabs() {
    const [key, setKey] = useState('home');
  
    return (
      <Tabs
        id="controlled-tab-example"
        activeKey={key}
        onSelect={(k) => setKey(k)}
      >
        <Tab className="pt-4"   eventKey="home" title="Main Skill">
       <h5> User experience design - UI/UX</h5>
            <p>Delight the user and make it work.</p>
        <h5>Web and user interface design - Development</h5>
        <p>Websites, web experiences, ...</p>
        <h5>Interaction design - Animation</h5>
        <p>I like to move it move it.</p>
                </Tab>
        <Tab className="pt-4" eventKey="awards" title="Awards">
       <h5> Awwwards.com - Winner</h5>
<p>2019 - 2020</p>
<h5>CSS Design Awards - Winner</h5>
<p>2017 - 2018</p>
<h5>Design nominees - site of the day</h5>
<p>2020- 2021</p>
        </Tab>
        <Tab className="pt-4 " eventKey="Experience"  title="Experience">
       <h5> Sr. Front-end Engineer - Google</h5>
<p>2018 - Current</p>
<h5>Front-end Engineer - Microsoft</h5>
<p>2017 - 2018</p>
<h5>Software Engineer - Alibaba</h5>
<p>2020- 2021</p>
        </Tab>
        <Tab className="pt-4" eventKey="Education" title="Education & Certification">
      <h5>  BSc In CSE - ABC University, Los Angeles, CA</h5>
<p>2020</p>
<h5>Diploma in Computer Science - Gamma Technical Institute</h5>
<p>2020</p>
<h5>Graphic Designer - ABC Institute, Los Angeles, CA</h5>
<p>2021</p>
        </Tab>
      </Tabs>
    );
  }
  export default  ControlledTabs