import React, { Component } from 'react'
import  ControlledTabs from '../Component/Tabs'
import Common from '../Component/Common'
import Arafat from "../Images/Arafat.jpg"
import ScrollAnimation from 'react-animate-on-scroll'
export default class About extends Component {
    render() {
        return (
           <div className="mb-sm-mb-sm-4 mb-lg-5">
                 <div className='row pt-lg-4 pt-md-3 mx-sm-auto'>
                <div className=" col-md-5 col-sm-10 text-center">
                <ScrollAnimation animateOnce={true} animateIn='flipInY'
  animateOut='flipOutY'>   <img width="80%"   src={Arafat} alt="arafat's pic" /> </ScrollAnimation>
                </div>
                <div className=" col-md-6 col-sm-10">
               <div className="text-center ">
               <Common  title="About Me" desc="Hi, My name is Easin Arafat.     
I have been working as a web designer and WordPress expert for a long time, and have done lots of work over that time. My skills cover many aspects of graphic design, web design, and development. " />
             < ControlledTabs />

               </div>
               
                </div>
            </div>
             
           </div>
        )
    }
}
