import React, { useState } from 'react'
import emailjs from 'emailjs-com';
export default function Contact() {
const [name , setName] = useState("")
const handleNameChange = (e)=>{
   setName(e.target.value)
}
const [email,setEmail] = useState('')
const handleEmailChange=(e)=>{
    setEmail(e.target.value)
}
const [text, setText]=useState('')
const handleText=(e)=>{
  setText(e.target.value)
}

const handleSubmit = (e)=>{

    e.preventDefault();
    emailjs.sendForm('service_etvhsai', 'template_8iqh9nb', e.target, 'user_ESsIeG1WYJBAcrKe0OQu6')
    .then((result) => {
        // console.log(result.text);
        document.getElementById("form-status").innerHTML="The Message has been sent, Thank You"
     
    }, (error) => {
        // console.log(error.text);
     document.getElementById("form-status").innerHTML="Oops,looks like an error occurred "
      
    });
     setName('')
     setEmail("")
     setText('')
   
}





    return ( <div className="form-style w-75 m-auto">
            <form id='my_form'  onSubmit={handleSubmit} className="row  my-contact-form">
            <div className="row pb-3 pt-4">
            <label htmlFor="inputText"  className="form-label pt-2 col-sm-2">Your Name</label>
    <input name="name" onChange={handleNameChange} value={name} required  type="text" className="form-control col-sm-10" placeholder="Enter Your Name" aria-label="First name" />
  </div>
 
  <div className="row  pb-3">
    <label  htmlFor="inputEmail" className="form-label pt-2 col-sm-2 ">Email</label>
  
    <input name="email" onChange={handleEmailChange} value={email} type="email" required placeholder="Enter Your Email" className="form-control col-sm-10" id="inputEmail4" />
  </div>
  <div className="row pb-3">
  <label htmlFor="exampleFormControlTextarea1" className="form-label pt-2 col-sm-2">Your Message</label>
  <textarea value={text} onChange={handleText} name="message" className="form-control col-sm-10" placeholder="Drop Us a Line" id="exampleFormControlTextarea1" rows="3"></textarea>
</div>

<p className="text-center "  id="form-status"></p>
  <div className="col-12 text-center pb-3 ">
    <button type="submit" className="btn btn-lg view-more-btn">SUbmit</button>

  </div>
</form >


        </div>
    )
}
