import React, { Component } from 'react'
import Common from '../Component/Common'
import {Link} from 'react-router-dom'
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';

import a from '../Images/Portfolio/1.jpg'
import b from '../Images/Portfolio/2.jpg'
import c from '../Images/Portfolio/3.jpg'
import d from '../Images/Portfolio/4.jpg'
import e from '../Images/Portfolio/5.jpg'
import f from '../Images/Portfolio/6.jpg'

const images=[a ,b,c,d,e,f]
 

export default class Portfolio extends Component {
    render() {
        return (
            <div className="mb-sm-mb-sm-4 mb-lg-5">
                <Common  title="My Latest Project" desc="These are the Screenshots of some projects that I had worked on" />
        
                <div className="row gx-2 mt-3">
                {images.map((image,index)=>{
          return  <div key={index} className="col-md-3   port-item mt-4 ">
          <LazyLoadImage  effect="blur" src={image} alt="client" />
          <div id="text"  >
           <div className="d-flex justify-content-center flex-column align-items-center">
        <h5 className="mt-5">Development</h5>
         <h3 className="p-4 text-center ">Getting tickets to the big show</h3>
         <Link className="btn btn-outline-warning text-white" to="/">View Details</Link>
      
   
           </div>
         
            
          </div>
      </div>
        } )}
                  
                 
</div>
                  
              
   <div className="text-center pt-5 mt-4"> <a target="_blank" rel="noreferrer" href="https://arafat003.github.io/Portfolio/"> <button className="view-more-btn btn btn-lg ">View more</button></a></div>  
   </div>  
        )
    }
}
