import React from 'react'
import Navbar from "react-bootstrap/Navbar"
import Nav from 'react-bootstrap/Nav'
import {NavLink} from 'react-router-dom'
export default function Mynav() {
    return (
      
  
    <div  className="container mx-auto "><Navbar id="navbar_top" className='navbar '  expand="md">
  <Navbar.Brand className='text-dark' > <NavLink className='nav-brand text-decoration-none' style={{color:"#F9004D"}} to="/"> EA </NavLink></Navbar.Brand>
  <Navbar.Toggle aria-controls="basic-navbar-nav" />
  <Navbar.Collapse id="basic-navbar-nav">
    <Nav className="ms-auto">
     
     <NavLink exact activeClassName="active-class  " className=' mx-3 nav-link' to="/">Home</NavLink>
      <NavLink exact activeClassName=" active-class " className=' mx-3 nav-link' to="/about">About</NavLink>
      <NavLink exact activeClassName=" active-class " className=' mx-3 nav-link' to="/service">Service</NavLink>
      <NavLink exact activeClassName=" active-class " className=' mx-3 nav-link' to="/portfolio">Portfolio</NavLink>
      <NavLink exact activeClassName=" active-class " className=' mx-3 nav-link' to="/blog">Blog</NavLink>
      <NavLink exact activeClassName=" active-class " className=' mx-3 nav-link' to="/contact">Contact</NavLink>
    </Nav>
  </Navbar.Collapse>
</Navbar></div>
    
 

    )
}

document.addEventListener("DOMContentLoaded", function(){
  window.addEventListener('scroll', function() {
      if (window.scrollY > 100) {
        document.getElementById('navbar_top').classList.add('fixed-top');
        document.getElementById('navbar_top').classList.add('container');
        document.getElementById('navbar_top').classList.add('nav-bar-scroll');
        // add padding top to show content behind navbar
     var navbar_height = document.querySelector('.navbar').offsetHeight;
        document.body.style.paddingTop = navbar_height + 'px';
      } else {
        document.getElementById('navbar_top').classList.remove('fixed-top');
        document.getElementById('navbar_top').classList.remove('nav-bar-scroll');
         // remove padding top from body
        document.body.style.paddingTop = '0';
      } 
  });
});


